@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes panLeft {
	from {
		transform: translateX(50px);
	}
	to {
		transform: translateX(0);
	}
}

@keyframes panRight {
	from {
		transform: translateX(-50px);
	}
	to {
		transform: translateX(0);
	}
}

@keyframes panDown {
	from {
		transform: translateY(50px);
	}
	to {
		transform: translateY(0);
	}
}

@keyframes panUp {
	from {
		transform: translateY(50px);
	}
	to {
		transform: translateY(0);
	}
}

.delayedFadeIn > * {
	opacity: 0;
	opacity: 1 \9; /* IE fallback */
	animation: fadeIn ease-in;
	animation-fill-mode: forwards;
	animation-duration: 1s;
}

.delayedFadeInPanLeft > * {
	opacity: 0;
	opacity: 1 \9; /* IE fallback */
	animation: fadeIn ease-in, panLeft ease-in;
	animation-fill-mode: forwards;
	animation-duration: 1s;
}

.delayedFadeInPanRight > * {
